import React, { useState } from 'react';
import { supabase } from '../supabase';

const EditableStarRating = ({ rating, onRatingChange }) => {
    const [hoverRating, setHoverRating] = useState(0);
    const stars = Array.from({ length: 5 }, (_, i) => i + 1);

    return (
        <div>
            {stars.map(starIndex => (
                <span
                    key={starIndex}
                    className="star"
                    style={{ cursor: 'pointer', color: (starIndex <= (hoverRating || rating)) ? 'gold' : 'gray' }}
                    onClick={() => onRatingChange(starIndex)}
                    onMouseEnter={() => setHoverRating(starIndex)}
                    onMouseLeave={() => setHoverRating(0)}
                >
                    ★
                </span>
            ))}
        </div>
    );
};

const AddOpinionForm = ({ productSku, productName, onSubmitSuccess }) => {
    const [newOpinion, setNewOpinion] = useState({ autor: '', komentarz: '', rating: 5, sku: productSku || '' });
    const [status, setStatus] = useState({ isLoading: false, error: null });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setNewOpinion(prevState => ({ ...prevState, [name]: value }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!newOpinion.autor || !newOpinion.komentarz) {
            setStatus({ isLoading: false, error: 'Wypełnij wszystkie pola.' });
            return;
        }

        setStatus({ isLoading: true, error: null });

        const opinionData = {
            autor: newOpinion.autor,
            komentarz: newOpinion.komentarz,
            rating: parseInt(newOpinion.rating, 10),
            product_sku: newOpinion.sku || null,
        };

        const { error } = await supabase.from('opinie').insert([opinionData]);

        if (error) {
            console.error("Error adding opinion: ", error);
            setStatus({ isLoading: false, error: 'Wystąpił błąd podczas dodawania opinii.' });
        } else {
            setStatus({ isLoading: false, error: null });
            setNewOpinion({ autor: '', komentarz: '', rating: 5, sku: productSku || '' });
            if (onSubmitSuccess) {
                onSubmitSuccess();
            }
        }
    };

    return (
        <div className="card mb-4">
            <div className="card-body">
                <h5 className="card-title">Dodaj swoją opinię</h5>
                {productName && <p className="text-muted">Oceniasz produkt: <strong>{productName}</strong></p>}
                <form onSubmit={handleSubmit}>
                    {!productSku && (
                        <div className="mb-3">
                            <label htmlFor="sku" className="form-label">SKU Produktu (opcjonalnie)</label>
                            <input type="text" className="form-control" id="sku" name="sku" value={newOpinion.sku} onChange={handleChange} />
                        </div>
                    )}
                    <div className="mb-3">
                        <label htmlFor="author" className="form-label">Twoje imię</label>
                        <input type="text" className="form-control" id="author" name="autor" value={newOpinion.autor} onChange={handleChange} required />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="text" className="form-label">Treść opinii</label>
                        <textarea className="form-control" id="text" name="komentarz" rows="3" value={newOpinion.komentarz} onChange={handleChange} required></textarea>
                    </div>
                    <div className="mb-3">
                        <label className="form-label">Ocena:</label>
                        <EditableStarRating
                            rating={newOpinion.rating}
                            onRatingChange={(newRating) => setNewOpinion(prev => ({ ...prev, rating: newRating }))}
                        />
                    </div>
                    
                    <button type="submit" className="btn btn-primary mt-3" disabled={status.isLoading}>
                        {status.isLoading ? 'Dodawanie...' : 'Dodaj Opinię'}
                    </button>
                    {status.error && <p className="text-danger mt-2">{status.error}</p>}
                </form>
            </div>
        </div>
    );
};

export default AddOpinionForm;
