import React, { useState, useEffect, useCallback } from 'react';
import { useParams, Link } from 'react-router-dom';
import { supabase } from '../supabase';
import StarRating from '../components/StarRating';
import AddOpinionForm from '../components/AddOpinionForm';
import { Helmet } from 'react-helmet-async';

// --- Helper Functions ---
const getPriceValue = (priceString) => {
    if (!priceString) return 0;
    const price = parseFloat(String(priceString).replace(',', '.').replace(' zł', ''));
    return isNaN(price) ? 0 : price;
};

const formatPriceDisplay = (priceValue) => {
    if (priceValue === null || priceValue === undefined) return '';
    const num = parseFloat(priceValue);
    if (isNaN(num)) return priceValue;
    if (num % 1 === 0) {
        return `${num} zł`;
    } else {
        return `${num.toFixed(2).replace('.', ',')} zł`;
    }
};

const getLowestPrice = (product) => {
    if (!product || !product.heightOptions || product.heightOptions.length === 0) return 0;
    const prices = product.heightOptions.map(opt => getPriceValue(opt.promoPrice || opt.price));
    return Math.min(...prices);
};

const hasPromotion = (product) => {
    if (!product || !product.heightOptions) return false;
    return product.heightOptions.some(opt => opt.promoPrice);
};

const colorMap = {
    "Biały": "#FFFFFF",
    "Zielony": "#28a745",
    "Żółty": "#ffc107",
    "Czarny": "#000000",
    "Szary": "#6c757d",
    "Jasny brąz": "#D2B48C",
    "Niebieski": "#007bff",
    "Czerwony": "#dc3545",
    "Jasny Brąz": "#D2B48C",
};

const shuffleArray = (array) => {
    let currentIndex = array.length, randomIndex;
    while (currentIndex !== 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
    }
    return array;
};

const ProductDetail = () => {
    const { id } = useParams();
    const [productsData, setProductsData] = useState([]);
    const [isLoadingProducts, setIsLoadingProducts] = useState(true);
    const product = productsData.find(p => String(p.id) === String(id));

    const [mainImage, setMainImage] = useState('');
    const [selectedOption, setSelectedOption] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [reviewsLoading, setReviewsLoading] = useState(true);
    const [showReviewForm, setShowReviewForm] = useState(false);
    const [modalProduct, setModalProduct] = useState(null);

    // resolve asset path: supports absolute http, data:, or project public paths
    const resolveAsset = (path) => {
        if (!path) return '';
        if (path.startsWith('http') || path.startsWith('data:')) return path;
        const prefix = process.env.PUBLIC_URL || '';
        if (path.startsWith('/')) return `${prefix}${path}`;
        return `${prefix}/${path}`;
    };

    const fetchProducts = useCallback(async () => {
        setIsLoadingProducts(true);
        try {
            const url = `${process.env.PUBLIC_URL || ''}/products.json`;
            const response = await fetch(url);
            if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
            const data = await response.json();
            setProductsData(Array.isArray(data) ? data : []);
        } catch (error) {
            console.error('Error fetching products:', error);
            setProductsData([]);
        } finally {
            setIsLoadingProducts(false);
        }
    }, []);

    const fetchReviews = useCallback(async () => {
        if (!product) return;
        setReviewsLoading(true);
        try {
            const { data, error } = await supabase
                .from('opinie')
                .select('*')
                .eq('product_sku', product.sku);
            if (error) {
                console.error('Error fetching reviews:', error);
                setReviews([]);
            } else {
                setReviews(data || []);
            }
        } catch (e) {
            console.error('Error fetching reviews:', e);
            setReviews([]);
        } finally {
            setReviewsLoading(false);
        }
    }, [product]);

    useEffect(() => {
        fetchProducts();
    }, [fetchProducts]);

    useEffect(() => {
        if (product) {
            setMainImage(product.mainImage || '');
            setSelectedOption((product.heightOptions && product.heightOptions[0]) || null);
            fetchReviews();
        }
    }, [product, fetchReviews]);

    const handleReviewSubmitSuccess = () => {
        setShowReviewForm(false);
        fetchReviews();
    };

    if (isLoadingProducts) {
        return <div className="container mt-5 pt-5 text-center"><p>Ładowanie produktu...</p></div>;
    }

    if (!product) {
        return <div className="container mt-5 pt-5 text-center"><h2>Produkt nie znaleziony</h2></div>;
    }

    const recommendedProducts = (productsData || [])
        .filter(p => p.category === product.category && String(p.id) !== String(product.id));
    const shuffled = shuffleArray(recommendedProducts).slice(0, 3);

    return (
        <>
            <Helmet>
                <title>{product.name} | Rex3D</title>
                <meta name="description" content={product.description} />
                <meta property="og:title" content={product.name} />
                <meta property="og:description" content={product.description} />
                <meta property="og:image" content={resolveAsset(product.mainImage)} />
                <meta property="og:url" content={window.location.href} />
            </Helmet>

            <div className="container mt-5 pt-5">
                <div className="row">
                    <div className="col-lg-7">
                        <img
                            src={resolveAsset(mainImage)}
                            className="img-fluid w-100 mb-3 rounded sticky-top"
                            style={{ top: '100px' }}
                            alt={`Główne zdjęcie ${product.name}`}
                        />
                        {product.gallery && product.gallery.length > 1 && (
                            <div className="row g-2">
                                {product.gallery.map((img, index) => (
                                    <div key={index} className="col-2">
                                        <img
                                            src={resolveAsset(img)}
                                            className={`img-fluid product-detail-gallery-thumbnail ${mainImage === img ? 'active' : ''}`}
                                            alt={`${product.name} - zdjęcie ${index + 1}`}
                                            onClick={() => setMainImage(img)}
                                        />
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>

                    <div className="col-lg-5">
                        <h1 className="display-5 fw-bold">{product.name}</h1>

                        <div className="fs-2 fw-bolder mb-2">
                            {selectedOption?.promoPrice ? (
                                <>
                                    <s className="text-muted me-2 fs-4">{formatPriceDisplay(selectedOption.price)}</s>
                                    <span>{formatPriceDisplay(selectedOption.promoPrice)}</span>
                                </>
                            ) : (
                                <span>{formatPriceDisplay(selectedOption?.price)}</span>
                            )}
                        </div>

                        <div className="mb-4">
                            <span className="badge bg-light text-dark fs-6">SKU: {product.sku}</span>
                        </div>

                        {product.heightOptions && product.heightOptions.length > 0 && (
                            <>
                                <h5 className="mt-4">Wybierz opcję:</h5>
                                <div className="btn-group flex-wrap mb-4" role="group">
                                    {product.heightOptions.map(option => (
                                        <button
                                            key={option.label}
                                            type="button"
                                            className={`btn product-detail-option-btn ${selectedOption?.label === option.label ? 'active' : ''}`}
                                            onClick={() => setSelectedOption(option)}
                                        >
                                            {option.label}
                                        </button>
                                    ))}
                                </div>
                            </>
                        )}

                        {product.colorOptions && product.colorOptions.length > 0 && (
                            <>
                                <h5 className="mt-4">Dostępne kolory:</h5>
                                <div className="d-flex flex-wrap mb-4">
                                    {product.colorOptions.map(color => (
                                        <span
                                            key={color}
                                            className="color-swatch-detail me-2 mb-2"
                                            style={{ backgroundColor: colorMap[color] || '#ccc' }}
                                            title={color}
                                        />
                                    ))}
                                </div>
                            </>
                        )}

                        <div className="accordion accordion-flush mt-4" id="productDetailsAccordion">
                            <div className="accordion-item">
                                <h2 className="accordion-header" id="headingDesc">
                                    <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseDesc" aria-expanded="true" aria-controls="collapseDesc">
                                        Opis
                                    </button>
                                </h2>
                                <div id="collapseDesc" className="accordion-collapse collapse show" aria-labelledby="headingDesc" data-bs-parent="#productDetailsAccordion">
                                    <div className="accordion-body">
                                        {product.description}
                                    </div>
                                </div>
                            </div>
                            <div className="accordion-item">
                                <h2 className="accordion-header" id="headingDetails">
                                    <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#collapseDetails" aria-expanded="false" aria-controls="collapseDetails">
                                        Dodatkowe informacje
                                    </button>
                                </h2>
                                <div id="collapseDetails" className="accordion-collapse collapse" aria-labelledby="headingDetails" data-bs-parent="#productDetailsAccordion">
                                    <div className="accordion-body">
                                        <p>Kategoria: {product.category}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <section className="py-5 bg-light">
                <div className="container">
                    <h2 className="section-title">Opinie o produkcie</h2>
                    {reviewsLoading ? (
                        <p className="text-center">Ładowanie opinii...</p>
                    ) : reviews.length > 0 ? (
                        <div className="row">
                            {reviews.slice(0, 4).map(review => (
                                <div key={review.id} className="col-md-6 mb-4">
                                    <div className="card h-100">
                                        <div className="card-body">
                                            <div className="d-flex justify-content-between">
                                                <h5 className="card-title">{review.autor}</h5>
                                                <StarRating rating={review.rating} />
                                            </div>
                                            <p className="card-text">{review.komentarz}</p>
                                            <small className="text-muted">{new Date(review.created_at).toLocaleDateString()}</small>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    ) : (
                        <p className="text-center text-muted">Brak opinii dla tego produktu.</p>
                    )}
                    <div className="text-center mt-4">
                        {!showReviewForm ? (
                            <button className="btn btn-primary" onClick={() => setShowReviewForm(true)}>
                                Napisz opinię
                            </button>
                        ) : (
                            <div className="mt-4 p-4 border rounded bg-white">
                                <AddOpinionForm
                                    productSku={product.sku}
                                    productName={product.name}
                                    onSubmitSuccess={handleReviewSubmitSuccess}
                                />
                                <button className="btn btn-sm btn-outline-secondary mt-2" onClick={() => setShowReviewForm(false)}>Anuluj</button>
                            </div>
                        )}
                         <Link to="/opinie" className="btn btn-outline-secondary ms-2">Zobacz wszystkie opinie</Link>
                    </div>
                </div>
            </section>

            {shuffled.length > 0 && (
                <section className="py-5">
                    <div className="container">
                        <h2 className="section-title">Polecane produkty</h2>
                        <div className="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
                            {shuffled.map(recProduct => {
                                const lowestPrice = getLowestPrice(recProduct);
                                const isPromo = hasPromotion(recProduct);
                                return (
                                    <div key={recProduct.id} className="col">
                                        <div className="card h-100">
                                            <div className="position-relative" style={{ overflow: 'hidden' }}>
                                                <img src={resolveAsset(recProduct.mainImage)} className="card-img-top product-card-img" alt={recProduct.name} />
                                                <div className="position-absolute top-0 start-0 p-2">
                                                    {recProduct.isNew && <span className="badge bg-success me-1">NOWOŚĆ</span>}
                                                    {isPromo && <span className="badge bg-danger me-1">PROMOCJA</span>}
                                                    {recProduct.isBestseller && <span className="badge bg-warning text-dark">BESTSELLER</span>}
                                                </div>
                                            </div>
                                            <div className="card-body text-center">
                                                <h5 className="card-title text-dark">{recProduct.name}</h5>
                                                <p className="card-text text-muted fw-bold">od {formatPriceDisplay(lowestPrice)}</p>
                                            </div>
                                            <div className="card-footer bg-transparent border-top-0 pb-3 text-center">
                                                <button onClick={() => setModalProduct(recProduct)} className="btn btn-sm btn-outline-secondary me-2">Szybki podgląd</button>
                                                <Link to={`/products/${recProduct.id}`} className="btn btn-sm btn-primary">Zobacz szczegóły</Link>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </section>
            )}

            {modalProduct && (
                <>
                    <div className="modal fade show" style={{ display: 'block' }} tabIndex="-1">
                        <div className="modal-dialog modal-lg modal-dialog-centered">
                            <div className="modal-content">
                                <div className="modal-header">
                                    <h5 className="modal-title">{modalProduct.name}</h5>
                                    <button type="button" className="btn-close" onClick={() => setModalProduct(null)}></button>
                                </div>
                                <div className="modal-body">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="position-relative">
                                                <img src={resolveAsset(modalProduct.mainImage)} className="img-fluid rounded" alt={modalProduct.name} />
                                                <div className="position-absolute top-0 start-0 p-2">
                                                    {modalProduct.isNew && <span className="badge bg-success me-1">NOWOŚĆ</span>}
                                                    {hasPromotion(modalProduct) && <span className="badge bg-danger me-1">PROMOCJA</span>}
                                                    {modalProduct.isBestseller && <span className="badge bg-warning text-dark">BESTSELLER</span>}
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <p>{modalProduct.description}</p>
                                            <h6>Opcje:</h6>
                                            <ul>
                                                {(modalProduct.heightOptions || []).map(opt => (
                                                    <li key={opt.label}>
                                                        {opt.label} - <strong>{opt.promoPrice ? <>
                                                            <s className="text-muted me-2">{formatPriceDisplay(opt.price)}</s>
                                                            <span className="text-danger">{formatPriceDisplay(opt.promoPrice)}</span>
                                                        </> : formatPriceDisplay(opt.price)}</strong>
                                                    </li>
                                                ))}
                                            </ul>
                                            <h6>Dostępne kolory:</h6>
                                            <div className="d-flex flex-wrap mb-4">
                                                {(modalProduct.colorOptions || []).map(color => (
                                                    <span
                                                        key={color}
                                                        className="color-swatch-detail me-2 mb-2"
                                                        style={{ backgroundColor: colorMap[color] || '#ccc' }}
                                                        title={color}
                                                    />
                                                ))}
                                            </div>
                                            <Link to={`/products/${modalProduct.id}`} className="btn btn-primary mt-3">Idź do strony produktu</Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="modal-backdrop fade show" onClick={() => setModalProduct(null)}></div>
                </>
            )}
        </>
    );
};

export default ProductDetail;
