import React from 'react';

const StarRating = ({ rating }) => {
    const numericRating = Number(rating) || 0;
    const stars = [];
    for (let i = 1; i <= 5; i++) {
        if (i <= numericRating) {
            stars.push(<span key={i} className="text-warning">★</span>); // Filled star
        } else {
            stars.push(<span key={i} className="text-muted">☆</span>); // Empty star
        }
    }
    return <div>{stars}</div>;
};

export default StarRating;
